.reviewList {
    margin-top: 25px;
list-style: none;
}

.reviewBox {
    padding: 25px;
    box-shadow: 0 1px 4px rgba(0, 0, 0, 1), -23px 0 20px -23px rgba(0, 0, 0, 0.8),
        23px 0 20px -23px rgba(0, 0, 0, 0.8), 0 0 40px rgba(0, 0, 0, 0.1) inset;
    border-radius: 20px;
}

.reviewBox:not(:last-child),
.reviewAuthor {
    margin-bottom: 20px;
}